<template>
  <div class="animated slideInLeft" style="animation-duration:100ms">
  	<b-card id="programmeform">
  			<b-tabs>
  				<b-tab id="programmetab" active :title="$ml.get('programme')">
				  	<b-form v-on:submit.prevent="addProgramme">
		  				<b-row v-if="createdProgramme == 'false'">
		  					<b-col>
				  				<label v-text="$ml.get('name')"></label>
								<b-form-input type="text" v-model="programme.programme_name" :placeholder="$ml.get('name')"></b-form-input>
							</b-col>
							<b-col>
				  				<label v-text="$ml.get('programmebranch')"></label>
								<b-form-input type="text" v-model="programme.programme_branch" :placeholder="$ml.get('programmebranch')"></b-form-input>
							</b-col>
							<b-col>
				  				<label v-text="$ml.get('programmeduration')"></label>
								<b-form-input type="number" v-model="programme.programme_duration" :placeholder="$ml.get('programmeduration')"></b-form-input>
							</b-col>
							<b-col>
				  				<label v-text="$ml.get('programmecredits')"></label>
								<b-form-input type="number" v-model="programme.programme_credits" :placeholder="$ml.get('programmecredits')"></b-form-input>
							</b-col>
							<b-col>
				  				<label v-text="$ml.get('validfrom')"></label>
								<b-form-input type="number" v-model="programme.valid_from" :placeholder="$ml.get('validfrom')"></b-form-input>
							</b-col>
							<b-col>
				  				<label v-text="$ml.get('validto')"></label>
				 				<b-form-input type="number" v-model="programme.valid_to" :placeholder="$ml.get('validto')"></b-form-input>
				 			</b-col>
			 			</b-row>
						<br>
						<b-button type="submit" size="sm" variant="primary" v-text="$ml.get('submit')"><i class="fa fa-dot-circle-o"></i></b-button>
						<div v-if="createdProgramme">
							<h1><span v-text="programme.programme_name"></span></h1>
							<b-button type="button" size="sm" variant="primary" v-text="$ml.get('addacademicterms')"><i class="fa fa-dot-circle-o"></i></b-button>
							<b-button type="submit" size="sm" variant="primary" @click="completeProgrammeCreation" v-text="$ml.get('complete')"><i class="fa fa-dot-circle-o"></i></b-button>
						</div>
		  			</b-form>
				</b-tab>
		  	</b-tabs>
  	</b-card>
  </div>
</template>
	
<script>

	//Imports
	import {acadUrl} from '@/apiUrl'
	import apiUrl from '@/apiUrl'
	import axios from 'axios'
	import Vue from 'vue'
	import VueNotifications from 'vue-notifications'
	import miniToastr from 'mini-toastr' 

	//Declarations
    var api = axios.create({
	  withCredentials :true
	})
	const toastTypes = {
	  success: 'success',
	  error: 'error',
	  info: 'info',
	  warn: 'warn'
	}
	const options = {
	  success: toast,
	  error: toast,
	  info: toast,
	  warn: toast
	}
	function toast ({title, message, type, timeout, cb}) {
	  return miniToastr[type](message, title, timeout, cb)
	}

	//Function Calls
	miniToastr.init({types: toastTypes})
	Vue.use(VueNotifications, options)

    //Vue Handler
	export default {
		name:"ProgrammeDetails",
		components:{

		},
		data : function () {
			return {
				//form data
				programme : {
					personal_details: {
						phone:''
					}
				},
				edit_pic:false,
				copyAddress:false,
				profile_pic:"",
				//Programme List
				programmes : [{name:"No Programmes",_id:"0",disabled:true}],
				//Academic-Term List
				academic_terms : [{name:"No Academic-Terms",_id:"0",disabled:true}]
			}
		},
		methods:{
			// Send Edit Request
			editprogramme(args) {
				console.log(this.programme)
				var programmeformdata = new FormData()
				if(this.programme.programme) {
					programmeformdata.append('programme',this.programme.programme)

				}
				if(this.programme.academic_term) {
					programmeformdata.append('academic_term',this.programme.academic_term)
				}
				programmeformdata.append('user_name',this.programme.user_name)
				programmeformdata.append('password',this.programme.password)
				programmeformdata.append('name',this.programme.personal_details.name)
				programmeformdata.append('email',this.programme.personal_details.email)
				programmeformdata.append('phone',this.programme.personal_details.phone)
				programmeformdata.append('profile_pic',this.programme.personal_details.profile_pic)
				programmeformdata.append('father_name',this.programme.personal_details.father_name)
				programmeformdata.append('mother_name',this.programme.personal_details.mother_name)
				programmeformdata.append('current_address',this.programme.personal_details.current_address)
				programmeformdata.append('corresspondence_address',this.programme.personal_details.corresspondence_address)
				api.post(`${acadUrl}stud/programme/create`,programmeformdata,{headers:{'Content-Type':'multipart/form-data'}}).then((programme) => {
					console.log(programme.data)
				})
			},
			//Copy address
			copyAddr(args) {
				if(args=="true") {
					this.programme.personal_details.corresspondence_address = this.programme.personal_details.current_address
				}
			}
		},
		async mounted(){
			var apilink = apiUrl.split('/api')
			console.log(this.$route.params)
			await api.get(`${acadUrl}prog/programme/view/one/${this.$route.params.progId}`).then((programme) => {
				this.programme = programme.data
			})
		},
		computed: {
			// Validate Fields
			validatePhone() {
				return this.programme.personal_details.phone.length==10 && this.programme.personal_details.phone>0
			}
		}
	};
</script>

